.card{
    display: flex;
    flex-direction: column;
    align-items: start;

    border: solid 2px var(--gray) ;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0,0,0,0.25);

    background-color: var(--white);
    padding: 10px 20px;
    margin: 1rem 0;

    color: black;
}

.card:hover{
    border-bottom-color: var(--gray-dark);
}

.card-id{
    color: var(--gray-dark);
}

.card-heading{
    color: black;
    font-weight: 600;
}

.card-tag{
    color: var(--gray-dark);

    border: solid 1px var(--gray) ;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.25);
    padding: 5px;
    margin: 6px 0;
}
